import React from 'react';
import { useAmmMap } from '../../../stores/Amm/AmmMap';
import { AmmDetail, CustomError, ErrorMap, } from '@loopring-web/component-lib/src/static-resource';
import { deepClone } from '../../../utils/obj_tools';
import { STATUS } from '../../../stores/constant';
import { useTokenMap } from '../../../stores/token';
import { useSocket } from '../../../stores/socket';
import { useTicker } from '../../../stores/ticker';
import { TickerMap, tickerService } from '../../../services/tickerService';

export function useAmmMapUI<R extends { [ key: string ]: any }, I extends { [ key: string ]: any }>({pageSize}: { pageSize: number }) {
    const [rawData, setRawData] = React.useState<Array<AmmDetail<any>> | []>([]);
    const [page, setPage] = React.useState<number>(1);
    const [keys, setKeys] = React.useState<string[]>([]);
    const {coinMap} = useTokenMap();
    const {
        ammMap,
        status: ammMapStatus,
        // errorMessage: errorAmmMap,
        statusUnset: ammMapStatusUnset,
        // updateAmmMap,
    } = useAmmMap();
    const {
        tickerMap,
        status: tickerStatus,
        // errorMessage: errorTickerMap,
        statusUnset: tickerStatusUnset,
        updateTickers,
    } = useTicker();
    const {
        status:socketStatus,
        // errorMessage: errorTickerMap,
        statusUnset: socketStatusUnset,
    } = useSocket();
    const updateRawData = React.useCallback( (tickerMap) => {
        try {
            const _ammMap = deepClone(ammMap);
            for (let tickerMapKey in tickerMap) {
                _ammMap[ tickerMapKey ].tradeFloat = tickerMap[ tickerMapKey ]
            }
            setRawData(Object.keys(_ammMap).map((ammKey: string) => {
                if (coinMap) {
                    _ammMap[ ammKey ][ 'coinAInfo' ] = coinMap[ _ammMap[ ammKey ][ 'coinA' ] ];
                    _ammMap[ ammKey ][ 'coinBInfo' ] = coinMap[ _ammMap[ ammKey ][ 'coinB' ] ];
                }
                return  _ammMap[ ammKey ];
            }) )
        } catch (error) {
            throw new CustomError({...ErrorMap.NO_TOKEN_MAP, options: error})
        }

    } ,[ammMap])


    const updateTickersUI = React.useCallback((_page) => {
        setPage(_page);
        if (ammMap && Object.keys(ammMap).length > 0) {
            const keys = []
            for (let i = (page - 1) * pageSize; i < Object.keys(ammMap).length && i < (page - 1) * pageSize + pageSize; i++) {
                keys.push(Object.keys(ammMap)[ i ]);
            }
            //TODO: SOCKET refixed key
            setKeys(keys);
            try{
               // socketStart({})
            }catch (error){
                updateTickers(keys);
            }


        }
    }, [ammMap, page, pageSize]);

    const subject = React.useMemo(() =>  tickerService.onSocket(),[]);
    React.useEffect(() => {
        if(ammMap && Object.keys(ammMap).length !== 0) {
            updateTickersUI(page)
        }
        const subscription = subject.subscribe(tickerMap => {
            if (tickerMap) {
                updateRawData(tickerMap);
            } else {
                updateTickers(keys);
            }
        });
        return ()=> subscription.unsubscribe();
    }, [subject]);

    //Websocket catch error do Api
    React.useEffect(() => {
        switch (socketStatus) {
            case "ERROR":
                console.log("ERROR", 'open websocket error get moment value from api ');
                socketStatusUnset();
                updateTickers(keys);
                break;
            default:
                break;
        }
    }, [socketStatus, socketStatusUnset]);

    React.useEffect(() => {
        switch (tickerStatus) {
            case "ERROR":
                console.log("ERROR", 'get ticker error,ui');
                tickerStatusUnset()
                break;
            case "PENDING":
                break;
            case "DONE":
                tickerStatusUnset()
                updateRawData(tickerMap)
                break;
            default:
                break;
        }
    }, [tickerStatus, tickerStatusUnset]);

    React.useEffect(() => {
        switch (ammMapStatus) {
            case "ERROR":
                console.log("ERROR", 'get ammMap error,ui');
                ammMapStatusUnset();
                break;
            case STATUS.PENDING:
                console.log("PENDING", 'get ammMap error,ui');
                break;
            case STATUS.DONE:
                ammMapStatusUnset();
                updateTickersUI(1)
                break;
            default :
                break;
        }
    }, [ammMapStatus, ammMapStatusUnset, updateTickersUI]);



    return {
        page,
        rawData,
        updateTickersUI,
    }
}

